.navbar {
  background-color: #f8f9fa;
  padding: 0.3rem 1rem;
  border-bottom: 1px solid #ddd;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  padding-right: 5rem;
  padding-left: 2rem;
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.navbar-logo {
  width: 40px;
  height: auto;
  margin-right: 1rem;
}

.navbar-brand h1 {
  font-size: 2rem;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
}

.navbar-links a {
  text-decoration: none;
  color: #333;
  font-weight: 500;
  transition: color 0.3s ease;
}

.navbar-links a:hover {
  color: #007bff;
}
